const navigation = document.querySelector('.nav')
const navigationContent = document.querySelector('.nav__content')
const navLinks = document.querySelector('.nav__links')
const links = document.querySelectorAll('.nav__links--link')
const burgerBtn = document.querySelector('.nav__burger')
const crossBtn = document.querySelector('.nav__cross')
const body = document.querySelector('body')
const logo = document.querySelector('.nav__logo')

function openMobNavigation() {
	navigationContent.classList.add('mobile-nav-active')
	burgerBtn.style.display = 'none'
	crossBtn.style.display = 'flex'
	navLinks.style.display = 'flex'
	body.style.overflowY = 'hidden'
}

function closeMobNavigation() {
	navigationContent.classList.remove('mobile-nav-active')
	burgerBtn.style.display = 'flex'
	crossBtn.style.display = 'none'
	navLinks.style.display = 'none'
	body.style.overflowY = 'visible'
}

function handleLinks(e) {
	links.forEach(link => link.classList.remove('active-link'))
	e.target.closest('a').classList.add('active-link')
	window.innerWidth < 750 && closeMobNavigation()
}

function changeNavigtionHeight() {
	if (window.innerWidth > 750 && window.scrollY > 120) {
		navigation.style.height = 8 + 'rem'
		logo.style.height = 8 + 'rem'
        navLinks.style.height = 8 + 'rem'
		links.forEach(link => (link.style.height = 8 + 'rem'))
	} else if (window.scrollY === 0 && window.innerWidth > 750) {
		navigation.style.height = 12 + 'rem'
		logo.style.height = 12 + 'rem'
        navLinks.style.height = 12 + 'rem'
		links.forEach(link => (link.style.height = 12 + 'rem'))
	}
}

burgerBtn.addEventListener('click', openMobNavigation)
crossBtn.addEventListener('click', closeMobNavigation)
links.forEach(link => link.addEventListener('click', handleLinks))
window.addEventListener('scroll', changeNavigtionHeight)
